
.top-nav{color:#222;position:relative;z-index:10000
}
.top-nav .bg{position:absolute;top:0;left:0;width:100%;height:42px;overflow:hidden;background-color:rgba(255,255,255,0.4);box-shadow:rgba(0,0,0,0.1) 0 1px 2px
}
.top-nav .bg .bg-ma{position:absolute;height:62px;padding:0 20px;top:-10px;left:-20px;width:100%;-webkit-filter:blur(5px);-ms-filter:blur(5px);filter:blur(5px);filter:progid:DXImageTransform.Microsoft.Blur(PixelRadius=5,MakeShadow=false);z-index:50;background:url("../assets/bg2.jpg") no-repeat center 0;background-position:center 0 !important;background-repeat:no-repeat
}
.top-nav .bg .bg-model{position:absolute;top:0;left:0;width:100%;height:42px;background-color:rgba(255,255,255,0.4);box-shadow:rgba(0,0,0,0.1) 0 1px 2px;z-index:99
}
.top-nav .nav-content{width:1160px;height:42px;margin:0 auto;position:relative;text-align:left;z-index:10000;font-size:13px
}
.top-nav .nav-content .nav{float:left;height:42px
}
.top-nav .nav-content .nav li{position:relative;float:left;line-height:42px;transition:all 0.3s ease
}
.top-nav .nav-content .nav li .show-content{display:none;position:absolute !important;top:42px;width:466px;height:256px;border-radius:0 0 4px 4px;box-shadow:1px 1px 3px rgba(0,0,0,0.4);position:relative;background:#fff;text-align:left;transition:all 0.3s ease
}
.top-nav .nav-content .nav li .show-content .left{width:265px;margin:12px 0 0 0;float:left
}
.top-nav .nav-content .nav li .show-content .left .banner{width:240px;height:128px;margin-left:12px
}
.top-nav .nav-content .nav li .show-content .left .banner a{position:relative;padding:0
}
.top-nav .nav-content .nav li .show-content .left .banner a img{width:240px;height:128px;border-radius:4px
}
.top-nav .nav-content .nav li .show-content .left .banner a span{width:220px;padding:0 10px;border-radius:4px;position:absolute;bottom:0px;left:0px;font-size:12px;line-height:30px;text-shadow:1px 1px 1px #000;color:#fff;background:url(http://static.biligame.net/biligametop/img/shadow.png) repeat-x
}
.top-nav .nav-content .nav li .show-content .left .brief{margin-top:6px
}
.top-nav .nav-content .nav li .show-content .left .brief a{position:relative;width:64px;height:104px;padding:6px 12px 0;display:block;float:left;overflow:hidden
}
.top-nav .nav-content .nav li .show-content .left .brief a:hover{background-color:#e5e9ef
}
.top-nav .nav-content .nav li .show-content .left .brief a img{width:64px;height:64px;border-radius:12px
}
.top-nav .nav-content .nav li .show-content .left .brief a span{position:absolute;top:72px;left:17px;width:4em;height:14px;margin:8px auto;display:block;font-size:14px;line-height:1;text-align:center;color:#222;overflow:hidden
}
.top-nav .nav-content .nav li .show-content .right{width:201px;min-height:234px;margin:12px 0 0 0;float:left;background:url(http://static.biligame.net/biligametop/img/line.png) no-repeat
}
.top-nav .nav-content .nav li .show-content .right .bbs{margin-top:-4px;margin-right:12px;display:inline-block;float:right;font-size:12px;color:#00a1d6
}
.top-nav .nav-content .nav li .show-content .right .all{padding:35px 0 0 6px;background:url(http://static.biligame.net/biligametop/img/title.png) no-repeat
}
.top-nav .nav-content .nav li .show-content .right .all a{width:163px;padding-left:16px;display:block;font-size:12px;line-height:28px;color:#222
}
.top-nav .nav-content .nav li .show-content .right .all a:hover{background-color:#e5e9ef
}
.top-nav .nav-content .nav li .show-content .imgdiv{width:220px;height:220px;display:block;position:absolute;bottom:0;left:442px
}
.top-nav .nav-content .nav li:hover .show-content{display:block
}
.top-nav .nav-content .nav li:first-child{padding:0;background-image:url("../assets/icons.png");background-repeat:no-repeat;background-position:-910px -74px;width:70px;margin-left:-10px
}
.top-nav .nav-content .nav li:first-child a{width:40px;padding-left:32px
}
.top-nav .nav-content .nav li a{display:inline-block;padding:0 15px;height:100%
}
.top-nav .nav-content .user-box{float:right;height:42px
}
.top-nav .nav-content .user-box ul{padding-left:10px;float:left;height:42px
}
.top-nav .nav-content .user-box ul li{float:left;line-height:42px;padding:0 10px;transition:all 0.3s ease
}
.top-nav .nav-content .user-box ul li:last-child{padding-right:0px
}
.top-nav .nav-content .user-box ul li a{display:inline-block;width:100%;height:100%
}
.top-nav .nav-content .user-box ul li a.i-link{margin:0;padding:0;text-align:center;width:58px;position:relative;z-index:10;background:url("../assets/b-post.png") center center no-repeat;height:48px;border-radius:0 0 5px 5px;color:#fff !important;font-size:14px
}
.top-nav .nav-content .user-box ul li a.i-link:hover{opacity:0.9
}
.top-nav .nav-content .user-box ul .s-line{float:left;display:inline-block;border-left:1px solid #222;height:12px;margin-top:16px;vertical-align:top
}
.top-nav .nav-content li:hover{background-color:rgba(255,255,255,0.2)
}
.top-nav .nav-content li.none:hover{background:none
}
@media screen and (max-width: 1400px){
.top-nav .nav-content{width:980px
}
}



